import React, { Component } from 'react';
import { ImgHolder, ItemDetails } from './general/style/style';
import { LHS, RHS } from './general/style/common';
import { FeaturedStoriesContainer } from './general/style/style';
//import { toJS } from 'mobx';


export default class FeaturedStories extends Component {

  onClickStory = (id, index, itemsList, nextPageUrl, deeplink, groupType) => {
    window["MAX_SWIPEABLE_MEMES_ITEMS"] = 1000;
    window["featuredItems"] = [];
    
    if(typeof(isIos)!="undefined" && !isIos()){
      for (let i = 0; itemsList && i < itemsList.length; i++) {
        window["featuredItems"].push({
            id: itemsList[i].id
        })
    }
    }else {
      window["featuredItems"] =itemsList;
    }
    if(typeof(openItem)!="undefined" && checkMinAndroidAppVersion('16.0.0'))
     openItem('openNews', id, index, '', nextPageUrl, 'featuredItems', deeplink);
    else if(typeof(openItem)!="undefined")
     openItem('openNews', id, groupType, index, '', nextPageUrl, 'featuredItems', deeplink);
  }

  render() {
    // console.log('Featured Stories: ',toJS(this.props.data));
    return (
      <FeaturedStoriesContainer onClick={() => this.onClickStory(this.props.data.id, this.props.data.index, this.props.data.featuredItems,
        this.props.data.nextPageUrl, this.props.data.deeplinkUrl, this.props.data.groupType)}
        isFallbackCase={this.props.isFallbackCase}
      >
      <ImgHolder height={this.props.data.imgHeight?this.props.data.imgHeight:"123px"}> 
        {this.props.data.isPlayBtn && <a href="javascript:void(0)" className='playBtn'>&nbsp;</a>}
        <img src={this.props.data.contentImage} alt={this.props.data.title}/>
      </ImgHolder>
        <ItemDetails className="featured-stories">
          {this.props.data.title && <p>{this.props.data.title}</p>}
          {this.props.data.isShareable ? <SharePanel data={this.props.data}/>:<NoSharePanel data={this.props.data}/>}
        </ItemDetails>
    </FeaturedStoriesContainer>);
  }
}

function SharePanel({data}) {

   const onClickShare = (e) => {
    e.preventDefault();
    e.stopPropagation();
    const story = data?.story;
    typeof (webShare) === "function" && story && webShare(story.title, story.shareUrl, '', data.type, '', story.id, story.type, '', '', story.source.langCode||'en', story.source.displayName, story.source.id, story.source.entityType, story.source.imageUrl);
  }

  // title, shareUrl, sectionName(x), type, group(x), itemId, assetType, idParamsId(x), idParamsItemId(x), langCode, idParamsNameSpace, idParamsNpId, entityType, sourceImageurl)

  return (
    <div className='share'>
      <LHS>
        {data && <ul>
          {data.source && <li>{data.source}</li>}
          {data.publishTime && <li className="sep"> .</li>}
          {data.publishTime && <li>{data.publishTime}</li>}
        </ul>}
      </LHS>
      {<RHS><a href="javascript:void(0)" className='whatsApp' onClick={(e)=>onClickShare(e)}>&nbsp;</a></RHS>}
    </div>
  )
}   

function NoSharePanel({ data }) {
  return (
    <>
    { data && <ul>
      {data.source && <li>{data.source}</li>}
      <li className="sep"> .</li>
      {data.publishTime && <li>{data.publishTime}</li>}
    </ul>
    }
    </>
  )
}

